"use client"

import type React from "react"

import { useState, useCallback } from "react"
import { Loader2 } from "lucide-react"
import { COLORS, DURATIONS } from "@/lib/constants"

/**
 * SECTION: API Terminal
 * Interactive terminal-styled form for API login demonstration
 */
export const ApiTerminal: React.FC = () => {
  const [logs, setLogs] = useState<string[]>(["> System Ready...", "> Waiting for user credentials..."])
  const [formData, setFormData] = useState({ username: "", password: "" })
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)

  const addLog = useCallback((msg: string) => {
    setLogs((prev) => [...prev, `> ${msg}`])
  }, [])

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()

    if (!formData.username || !formData.password) {
      addLog("ERROR: Username and Password required.")
      return
    }

    setLoading(true)
    addLog("Initiating connection to endpoint...")
    addLog(`POST /api/login { user: "${formData.username}" }`)

    // Simulate API call
    setTimeout(() => {
      setLoading(false)
      setSuccess(true)
      addLog("STATUS: 200 OK")
      addLog("Token received: eyJhbGciOiJIUzI1Ni...")
      addLog("ACCESS GRANTED. Welcome aboard.")
    }, DURATIONS.apiTerminalDelay)
  }

  return (
    <section
      className="min-h-screen py-32 px-6 md:px-20 flex flex-col items-center justify-center relative overflow-hidden"
      style={{ backgroundColor: COLORS.dark }}
    >
      {/* Background Grid */}
      <div
        className="absolute inset-0 [mask-image:radial-gradient(ellipse_60%_60%_at_50%_50%,#000_70%,transparent_100%)]"
        style={{
          backgroundImage:
            "linear-gradient(rgba(255,255,255,0.03) 1px, transparent 1px), linear-gradient(90deg, rgba(255,255,255,0.03) 1px, transparent 1px)",
          backgroundSize: "40px 40px",
        }}
      />

      {/* Content */}
      <div className="z-10 w-full max-w-5xl">
        {/* Header */}
        <div className="mb-10 text-center md:text-left">
          <h2 className="text-4xl md:text-6xl font-black text-white mb-2">API CHALLENGE</h2>
          <p className="text-gray-400 font-mono">Endurance Test Level 3: Data Fetching</p>
        </div>

        {/* Terminal Grid */}
        <div
          className="grid grid-cols-1 md:grid-cols-2 gap-0 rounded-lg overflow-hidden shadow-2xl border"
          style={{
            backgroundColor: "#050505",
            borderColor: COLORS.gray[800],
          }}
        >
          {/* Form Side */}
          <div className="p-10 border-r flex flex-col justify-center" style={{ borderColor: COLORS.gray[800] }}>
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Username Input */}
              <div>
                <label
                  className="font-mono text-xs uppercase tracking-widest mb-2 block"
                  style={{ color: COLORS.accent }}
                >
                  Identity
                </label>
                <input
                  type="text"
                  className="w-full p-4 text-white focus:outline-none transition-colors font-mono rounded"
                  placeholder="username"
                  disabled={success}
                  value={formData.username}
                  onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                  style={{
                    backgroundColor: "#111",
                    borderWidth: "1px",
                    borderColor: COLORS.gray[700],
                    focusBorderColor: COLORS.accent,
                  }}
                />
              </div>

              {/* Password Input */}
              <div>
                <label
                  className="font-mono text-xs uppercase tracking-widest mb-2 block"
                  style={{ color: COLORS.accent }}
                >
                  Passkey
                </label>
                <input
                  type="password"
                  className="w-full p-4 text-white focus:outline-none transition-colors font-mono rounded"
                  placeholder="•••••••"
                  disabled={success}
                  value={formData.password}
                  onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                  style={{
                    backgroundColor: "#111",
                    borderWidth: "1px",
                    borderColor: COLORS.gray[700],
                  }}
                />
              </div>

              {/* Submit Button */}
              <button
                disabled={loading || success}
                className="w-full py-4 font-bold uppercase tracking-wider transition-all duration-300 rounded"
                style={{
                  backgroundColor: success ? "#16a34a" : COLORS.accent,
                  color: success ? COLORS.black : COLORS.white,
                  opacity: loading || success ? 0.9 : 1,
                }}
                onMouseEnter={(e) => {
                  if (!success && !loading) {
                    e.currentTarget.style.backgroundColor = COLORS.white
                    e.currentTarget.style.color = COLORS.black
                  }
                }}
                onMouseLeave={(e) => {
                  if (!success && !loading) {
                    e.currentTarget.style.backgroundColor = COLORS.accent
                    e.currentTarget.style.color = COLORS.white
                  }
                }}
              >
                {loading ? (
                  <>
                    <Loader2 className="inline mr-2 h-4 w-4 animate-spin" />
                    Transmitting...
                  </>
                ) : success ? (
                  "Connected"
                ) : (
                  "Execute Login"
                )}
              </button>
            </form>
          </div>

          {/* Terminal Side */}
          <div
            className="bg-black p-6 font-mono text-xs md:text-sm overflow-y-auto h-[400px] flex flex-col"
            style={{ backgroundColor: COLORS.black }}
          >
            {/* Terminal Buttons */}
            <div className="flex gap-2 mb-4">
              <div className="w-3 h-3 rounded-full bg-red-500" />
              <div className="w-3 h-3 rounded-full bg-yellow-500" />
              <div className="w-3 h-3 rounded-full bg-green-500" />
            </div>

            {/* Logs */}
            <div className="flex-1 space-y-2">
              {logs.map((log, i) => (
                <div
                  key={i}
                  style={{
                    color: log.includes("ERROR") ? "#ef4444" : log.includes("GRANTED") ? "#4ade80" : "#9ca3af",
                  }}
                >
                  {log}
                </div>
              ))}
              {loading && (
                <div className="animate-pulse" style={{ color: COLORS.accent }}>
                  _
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
